
:root {
  --header-height: 55px;
  --footer-height: 55px;
}

body {
  margin: 0;
  font-family: 'Roboto';
font-style: normal;
font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
}

* {
  box-sizing: border-box;
}

@media screen and (max-width: 375px)  {
  body {
    overflow-y: scroll;
    background-color: #0079bf;
  }
}